.demo {
  font-family: sans-serif;
  border: 1px solid #eee;
  border-radius: 2px;
  padding: 10px 15px;
  margin-top: 1em;
  margin-bottom: 10px;
  user-select: none;
  overflow-x: auto;

  span {
    border: 1px solid grey;
    padding: 5px;
    border-radius: 5px;
  }
  .red {
    background: red;
    padding: 5px;
  }
  .green {
    background: green;
    padding: 5px;
  }
  .yellow {
    background: yellow;
    padding: 5px;
  }
}

#header li {
  display: inline;
  background: rgb(104, 61, 61);
}
